
* {
  box-sizing: border-box;
}

.hidden-accessibly {
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.focus,
:focus {
  box-shadow: 0px 0px 0px 3px var(--color-focus-primary);
  border-radius: 5px;
  outline: none;
}
